import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";

export const getTimeAsync = createAsyncThunk('time/getTime',
(zone,thunkAPI)=>{
    try {
        return axios.get(`http://worldtimeapi.org/api/timezone/${zone}`);
    } catch (error) {
        console.log(error);
        return;
    }
}
);

export const clockSlice = createSlice({
  name: 'clock',
  initialState: {
    unix: '1704489411',
    isPaused: false,
    zone: 'Africa/Abidjan'
  },
  reducers: {
    togglePause: (state) => {
      state.isPaused = !state.isPaused;
    },
    increment: (state) => {
      state.unix = ++state.unix;
    },
  },extraReducers:(builder)=>{
    builder.addCase(getTimeAsync.fulfilled, (state,action)=>{
        state.unix = action.payload.data.unixtime;
        state.zone = action.payload.data.timezone;
    })
  }
});


export const clockReducer = clockSlice.reducer;

export const {setCurrentTime, togglePause, increment} = clockSlice.actions;

export const selectCurrentTime = (state) => state.clockReducer.unix;

export const selectTimeZone = (state) => state.clockReducer.zone;

export const selectIsPaused = (state) => state.clockReducer.isPaused;
