import React from 'react';
import { useSelector } from 'react-redux';
import { selectUserList } from '../redux/reducers/userReducer';
import { useParams } from 'react-router-dom';
import styles from './peopleDirectory.module.css';

function UserDetails() {
  const users = useSelector(selectUserList);
  const params = useParams();
  const selectedUser = users.filter(user => user.id === parseInt(params.userId));

  return (
    <div className={styles.userdetails}>
      <div className={styles.portion}>
        <div><p>Name: {selectedUser[0].name}</p></div>
      <div>
      Username: {selectedUser[0].username} | Catch Phrase: {selectedUser[0].company.catchPhrase}
      </div>
      </div>
      <div className={styles.portion}>
      <div><p>Address: {selectedUser[0].address.city}, {selectedUser[0].address.street}, {selectedUser[0].address.suite}</p></div>
      <div>Email: {selectedUser[0].email} | Phone: {selectedUser[0].phone}
      </div>
      </div>
    </div>
  );
}

export default UserDetails;
