import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { selectIsPaused, getTimeAsync, selectCurrentTime, increment, selectTimeZone } from '../redux/reducers/clockReducer';
import { selectCountryList, getCountryListAsync } from '../redux/reducers/countryReducer';
import moment from 'moment-timezone';

function Clock() {
  const dispatch = useDispatch();
  const isPaused = useSelector(selectIsPaused);
  const countryList = useSelector(selectCountryList);
  const unixTime = useSelector(selectCurrentTime);
  const zone = useSelector(selectTimeZone);
  let [formattedTime, setFormattedTime] = useState('00:00:00');

  useEffect(()=>{
    dispatch(getCountryListAsync());
  },[dispatch]);
  
  useEffect(()=>{
    let interval;
  if(!isPaused){
    interval = setInterval(()=>{
      let time = new Date(unixTime*1000).toLocaleTimeString();
      let format = 'HH:mm:ss';
      setFormattedTime(moment(time, format).tz(zone).format(format));
      dispatch(increment());
    },1000);
  }
  return () => {
        clearInterval(interval);
      };
},[dispatch, isPaused, unixTime, zone]);

  return (
    <div>
      <p>Country &nbsp;&nbsp;
      <select onChange={(e)=>dispatch(getTimeAsync(e.target.value))}>
      {countryList.map(country=>
        <option>{country}</option>
      )}
      </select>&nbsp;&nbsp;
      {formattedTime}
      </p>
    </div>
  );
}

export default Clock;
