import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";

export const getCountryListAsync = createAsyncThunk('country/getCountryList',
()=>{
    try {
        return axios.get('http://worldtimeapi.org/api/timezone');
    } catch (error) {
        console.log(error);
        return;
    }
}
);

export const countrySlice = createSlice({
  name: 'country',
  initialState: {
    countryList: [],
  },
  reducers: {
    // setCountryList: (state, action) => {
    //   state.countryList = action.payload;
    // },
  },
  extraReducers: (builder)=>{
    builder.addCase(getCountryListAsync.fulfilled, (state,action)=>{
      state.countryList = [...action.payload.data];
    })
}
});

export const countryReducer = countrySlice.reducer;

export const { setCountryList } = countrySlice.actions;

export const selectCountryList = (state) => state.countryReducer.countryList;
