import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { getInitialStateAsync, selectUserList } from '../redux/reducers/userReducer';
import { NavLink } from 'react-router-dom';
import {getPostsAsync, selectPostList} from '../redux/reducers/postReducer';
import styles from './peopleDirectory.module.css';

function PeopleDirectory() {
  const dispatch = useDispatch();
  const userList = useSelector(selectUserList);
  const posts = useSelector(selectPostList);

  useEffect(() => {
      dispatch(getInitialStateAsync());
      dispatch(getPostsAsync());
  }, [dispatch]);

  return (
    <div className={styles.container}>
      <h1>People Directory</h1>
      <ul>
        {userList.map((user) => (
          <NavLink to={`user/${user.id}`} style={() => ({
            color: 'black',
            textDecoration: 'none'
          })}><li key={user.id} id={styles.card} >
            <div>Name: {user.name}</div>
            <div>Posts: {posts.filter(posts => posts.userId === user.id).length}</div>
          </li>
          </NavLink>
        ))}
      </ul>
    </div>
  );
}

export default PeopleDirectory;
