import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useParams, useNavigate } from 'react-router-dom';
import { selectUserList } from '../redux/reducers/userReducer';
import { togglePause, selectIsPaused } from '../redux/reducers/clockReducer';
import Clock from './Clock';
import UserDetails from './UserDetails';
import PostsDisplay from './PostsDisplay';
import styles from './peopleDirectory.module.css';

function UserProfile() {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { userId } = useParams();
  const users = useSelector(selectUserList);
  const selectedUser = users.filter(user => user.id === parseInt(userId));
  const isPaused = useSelector(selectIsPaused);

  const handlePauseToggle = () => {
    dispatch(togglePause());
  };

  const handleGoBack = () => {
    navigate('/');
  };

  return (
    <div className={styles.profilepage}>
      <div className={styles.clock}>
      <button onClick={handleGoBack} id={styles.backbtn}>Go Back</button>
      <Clock />
      <button onClick={handlePauseToggle} id={styles.startPausebtn}>{isPaused ? 'Start' : 'Pause'}</button>
      </div>
      <h1>{selectedUser[0].name}'s Profile Page</h1>
      <UserDetails />
      <PostsDisplay />
      
    </div>
  );
}

export default UserProfile;
