import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import axios from 'axios';

export const getInitialStateAsync = createAsyncThunk('user/getInitialState',
  ()=>{
    try {
        return axios.get('https://jsonplaceholder.typicode.com/users');
    } catch (error) {
        console.log(error);
        return;
    }
}
);

export const userSlice = createSlice({
  name: 'user',
  initialState: {
    userList: [],
    selectedUser: null,
  },
  reducers: {
    
  },
  extraReducers:(builder)=>{
    builder.addCase(getInitialStateAsync.fulfilled, (state,action)=>{
        state.userList = [...action.payload.data];
    })
}
});


export const userReducer = userSlice.reducer;

export const { setUserList, setSelectedUser } = userSlice.actions;

export const selectUserList = (state) => state.userReducer.userList;
