import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import axios from 'axios';

export const getPostsAsync = createAsyncThunk('posts/getPosts',
  ()=>{
    try {
        return axios.get('https://jsonplaceholder.typicode.com/posts');
    } catch (error) {
        console.log(error);
        return;
    }
}
);


export const postSlice = createSlice({
  name: 'post',
  initialState: {
    postList: []
  },
  reducers: {
    
  },
  extraReducers:(builder)=>{
    builder.addCase(getPostsAsync.fulfilled, (state,action)=>{
        state.postList = [...action.payload.data];
    })
}
});

export const postReducer = postSlice.reducer;

export const { setPostList } = postSlice.actions;

export const selectPostList = (state) => state.postReducer.postList;
